import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
    navigation: {
        display: 'flex',
        marginRight: 90
    },
    navItem: {
        marginRight: 40,
        cursor: 'pointer'
    }
}));

export default function Navbar(props) {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        MyContact
                    </Typography>
                    <div className={classes.navigation}>
                        <Typography onClick={() => props.history.push('/add-contact')} variant="h6" className={classes.title, classes.navItem}>
                            Add Contact
                        </Typography>
                        <Typography onClick={() => props.history.push('/favourite-contact')} variant="h6" className={classes.title, classes.navItem}>
                            Favourite Contact
                        </Typography>
                        <Typography onClick={() => props.history.push('/all-contact')} variant="h6" className={classes.title, classes.navItem}>
                            All Contact
                        </Typography>
                    </div>
                    <Button color="inherit" onClick={() => alert('Feature not implemented yet')}>Login</Button>
                </Toolbar>
            </AppBar>
        </div>
    );
}
